import React, { Fragment } from "react";

const TdHead = ({ d, i, type, mode }) =>
  type === "thead" ? (
    <Fragment>
      {mode === 'year' ? <th>i</th> : null}
      <th className="th-label">{mode === "year" ? "クラブ" : "年"}</th>
      <th>所属</th>
      <th>順位</th>
    </Fragment>
  ) : (
    <Fragment>
      {mode === 'year' ? <th>{i + 1}</th> : null}
      <th className="th-label">
        {mode === "year" ? d["クラブ"] : d["年"]}
      </th>
      <th>{d["所属"]}</th>
      <th>{d["順位"]}</th>
    </Fragment>
  );

export default TdHead;



